<template>
  <div v-if="pics">
    <b-carousel
      id="carousel1"
      style="text-shadow: 1px 1px 2px #333;"
      controls
      indicators
      background="#ababab"
      :interval="6000"
      img-width="100%"
      img-height="480"
      v-model="slide"
      @sliding-start="onSlideStart"
      @sliding-end="onSlideEnd"
    >
      <b-carousel-slide class="slide-img" v-for="(pic,idx) in pics" :key="idx" :img-src="pic"></b-carousel-slide>
    </b-carousel>
  </div>
</template>

<script>
import img1 from "@/assets/imgs/photo5.jpeg";
import img2 from "@/assets/imgs/photo4.jpeg";
import img3 from "@/assets/imgs/photo3.jpeg";
// import img4 from "@/assets/imgs/photo2.jpeg";
import img5 from "@/assets/imgs/photo1.jpeg";
export default {
  data() {
    return {
      pics: [img1, img3, img2, img5],
      slide: 0,
      sliding: null
    };
  },
  methods: {
    onSlideStart() {
      this.sliding = true;
    },
    onSlideEnd() {
      this.sliding = false;
    }
  }
};
</script>

<style scoped lang="scss">
.slide-img {
}

.carousel-inner {
}
</style>