<template>
  <header>
    <section class="nav-container">
      <router-link class="logo" to="/">
        <img src="@/assets/imgs/logo.png">
      </router-link>
      <nav>
        <router-link to="/contact">{{ $t('contact') }}</router-link>&nbsp;|
        <router-link to="/about">{{ $t('about') }}</router-link>&nbsp;
        <img src="@/assets/imgs/English.png" width="20" @click="onSetLang('English')">
        <img src="@/assets/imgs/hebrew.png" width="20" @click="onSetLang('Hebrew')">
      </nav>
    </section>
  </header>
</template>

<script>
export default {
  methods: {
    onSetLang(lang) {
      if (lang === "English") {
        this.$i18n.locale = "en";
      } else {
        this.$i18n.locale = "he";
      }
      this.$store.dispatch("setLang", { lang });
    }
  },
  created() {
    this.$i18n.locale = "he";
  }
};
</script>

<style scoped lang="scss">
header {
  background: rgb(127, 180, 197);
}

.nav-container {
  height: 80px;
  margin: auto;
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 15px;
  font-weight: bold;
  a {
    text-decoration: none;
    color: #222222;
    &:hover {
      color: brown;
    }
  }
}
nav {
  img {
    cursor: pointer;
    margin-left: 2px;
    &:hover {
      opacity: 0.8;
    }
  }
}

.logo {
  img {
    height: 25px;
  }
}

@media (min-width: 500px) {
  .logo {
    img {
      height: 45px;
    }
  }
  .nav-container {
    font-size: 24px;
  }
}
</style>
