import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lang: 'he'
  },
  getters: {
    lang: (state) => state.lang
  },
  mutations: {
    setLang(state, { lang }) {
      state.lang = lang
    }
  },
  actions: {
    setLang({ commit }, { lang }) {
      commit({ type: 'setLang', lang })
    }
  }
})
