import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/assets/scss/main.scss'
import BootstrapVue from 'bootstrap-vue'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
import * as VueGoogleMaps from 'vue2-google-maps'
import { library } from '@fortawesome/fontawesome-svg-core'
import { faCoffee, faPhone, faSnowflake, faWheelchair, faDog, faWifi, faParking, faTv, faSwimmer, faBath, faAt, faHome} from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import i18n from './i18n'

library.add(faCoffee, faPhone, faSnowflake, faWheelchair, faDog, faWifi, faParking, faTv, faSwimmer, faBath, faAt, faHome)

Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.use(BootstrapVue);
Vue.config.productionTip = false

Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyB6n7iEYMeOq1PxgcjFtjgKLiuUA5_Oc20',
    language: 'he',
    libraries: 'places',
  },
})

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
